"use client"

import { useEffect, useState } from "react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { DropdownMenu, DropdownMenuContent, DropdownMenuItem, DropdownMenuTrigger } from "@/components/ui/dropdown-menu"
import { Dialog, DialogContent, DialogFooter, DialogHeader, DialogTitle } from "@/components/ui/dialog"
import { Label } from "@/components/ui/label"
import { Book, Headphones, Heart, Crown, Search, MoreHorizontal, ExternalLink, Trash2, Pencil, Save } from "lucide-react"
import { deleteResource, fetchResources, type ResourceRecord, updateResource } from "@/lib/api"
import { useToast } from "@/components/ui/use-toast"

type ResourceType = "all" | "novel" | "audio" | "comic"

const typeConfig = {
  novel: { icon: Book, label: "小说", className: "bg-primary/10 text-primary" },
  audio: { icon: Headphones, label: "音频", className: "bg-chart-2/10 text-chart-2" },
  comic: { icon: Heart, label: "漫画", className: "bg-chart-5/10 text-chart-5" },
}

const filterLabels: Record<ResourceType, string> = {
  all: "全部",
  novel: "小说",
  audio: "音频",
  comic: "漫画",
}

export function ResourceList() {
  const [filter, setFilter] = useState<ResourceType>("all")
  const [search, setSearch] = useState("")
  const [resources, setResources] = useState<ResourceRecord[]>([])
  const [loading, setLoading] = useState(false)
  const [editMap, setEditMap] = useState<
    Record<
      string,
      {
        title: string
        link: string
        dirty: boolean
        saving: boolean
      }
    >
  >({})
  const [editing, setEditing] = useState<ResourceRecord | null>(null)
  const [editTitle, setEditTitle] = useState("")
  const [editLink, setEditLink] = useState("")
  const [savingEdit, setSavingEdit] = useState(false)
  const { toast } = useToast()

  useEffect(() => {
    let ignore = false
    const load = async () => {
      try {
        setLoading(true)
        const data = await fetchResources(filter)
        if (!ignore) {
          setResources(data)
          const nextMap: typeof editMap = {}
          data.forEach((item) => {
            nextMap[item.id] = {
              title: item.title,
              link: item.link,
              dirty: false,
              saving: false,
            }
          })
          setEditMap(nextMap)
        }
      } catch (error) {
        const message = error instanceof Error ? error.message : "加载失败"
        toast({
          title: "读取资源失败",
          description: message,
          variant: "destructive",
        })
      } finally {
        if (!ignore) {
          setLoading(false)
        }
      }
    }
    load()
    return () => {
      ignore = true
    }
  }, [filter, toast])

  const filteredResources = resources.filter((r) => {
    const matchesType = filter === "all" || r.type === filter
    const matchesSearch = r.title.toLowerCase().includes(search.toLowerCase())
    return matchesType && matchesSearch
  })

  const removeResource = async (id: string) => {
    try {
      await deleteResource(id)
      setResources((prev) => prev.filter((item) => item.id !== id))
      setEditMap((prev) => {
        const clone = { ...prev }
        delete clone[id]
        return clone
      })
      toast({ title: "已删除资源" })
    } catch (error) {
      const message = error instanceof Error ? error.message : "删除失败"
      toast({
        title: "删除失败",
        description: message,
        variant: "destructive",
      })
    }
  }

  const handleFieldChange = (id: string, field: "title" | "link", value: string) => {
    setEditMap((prev) => {
      const current = prev[id] ?? {
        title: "",
        link: "",
        dirty: false,
        saving: false,
      }
      return {
        ...prev,
        [id]: {
          ...current,
          [field]: value,
          dirty: true,
        },
      }
    })
  }

  const handleRowSave = async (resource: ResourceRecord) => {
    const entry = editMap[resource.id]
    if (!entry || entry.saving || !entry.dirty) {
      return
    }
    if (!entry.title.trim() || !entry.link.trim()) {
      toast({ title: "请填写完整信息", variant: "destructive" })
      return
    }
    try {
      setEditMap((prev) => ({
        ...prev,
        [resource.id]: { ...entry, saving: true },
      }))
      const updated = await updateResource(resource.id, {
        title: entry.title.trim(),
        jump_url: entry.link.trim(),
      })
      setResources((prev) =>
        prev.map((item) => (item.id === updated.id ? updated : item)),
      )
      setEditMap((prev) => ({
        ...prev,
        [resource.id]: {
          title: updated.title,
          link: updated.link,
          dirty: false,
          saving: false,
        },
      }))
      toast({ title: "已更新资源" })
    } catch (error) {
      const message = error instanceof Error ? error.message : "更新失败"
      toast({
        title: "更新失败",
        description: message,
        variant: "destructive",
      })
      setEditMap((prev) => ({
        ...prev,
        [resource.id]: { ...entry, saving: false },
      }))
    }
  }

  const openEdit = (resource: ResourceRecord) => {
    if (resource.type === "comic") {
      toast({
        title: "暂不支持",
        description: "漫画请通过上传流程重新发布。",
        variant: "destructive",
      })
      return
    }
    setEditing(resource)
    setEditTitle(resource.title)
    setEditLink(resource.link)
  }

  const handleEditSave = async () => {
    if (!editing) return
    if (!editTitle.trim() || !editLink.trim()) {
      toast({ title: "请输入完整信息", variant: "destructive" })
      return
    }
    try {
      setSavingEdit(true)
      const updated = await updateResource(editing.id, {
        title: editTitle.trim(),
        jump_url: editLink.trim(),
      })
      setResources((prev) =>
        prev.map((item) => (item.id === updated.id ? updated : item)),
      )
      toast({ title: "已更新资源" })
      setEditing(null)
    } catch (error) {
      const message = error instanceof Error ? error.message : "更新失败"
      toast({ title: "更新失败", description: message, variant: "destructive" })
    } finally {
      setSavingEdit(false)
    }
  }

  return (
    <>
      <Card className="w-full">
        <CardHeader>
        <CardTitle className="text-card-foreground">资源列表</CardTitle>
        <div className="flex flex-col sm:flex-row gap-3 mt-4">
          <div className="relative flex-1">
            <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
            <Input
              placeholder="搜索资源..."
              value={search}
              onChange={(e) => setSearch(e.target.value)}
              className="pl-9"
            />
          </div>
          <div className="flex gap-1">
            {(["all", "novel", "audio", "comic"] as const).map((type) => (
              <Button
                key={type}
                variant={filter === type ? "default" : "outline"}
                size="sm"
                onClick={() => setFilter(type)}
              >
                {filterLabels[type]}
              </Button>
            ))}
          </div>
        </div>
      </CardHeader>
      <CardContent className="space-y-4">
        <p className="text-sm text-muted-foreground px-1">
          小说和音频默认免费；可在此直接修改标题与链接，并保存到数据库。
        </p>
        <div className="rounded-lg border border-border overflow-hidden px-1">
          <Table>
            <TableHeader>
              <TableRow className="bg-muted/50">
                <TableHead className="w-1/4">标题</TableHead>
                <TableHead className="w-1/4">Telegram 链接</TableHead>
                <TableHead>类型</TableHead>
                <TableHead>状态</TableHead>
                <TableHead>创建日期</TableHead>
                <TableHead className="w-48 text-right">操作</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {filteredResources.map((resource) => {
                const config = typeConfig[resource.type]
                const Icon = config.icon
                const entry = editMap[resource.id]
                return (
                  <TableRow key={resource.id}>
                    <TableCell className="font-medium">
                      <Input
                        value={entry?.title ?? resource.title}
                        onChange={(e) => handleFieldChange(resource.id, "title", e.target.value)}
                      />
                    </TableCell>
                    <TableCell>
                      <Input
                        value={entry?.link ?? resource.link}
                        onChange={(e) => handleFieldChange(resource.id, "link", e.target.value)}
                      />
                    </TableCell>
                    <TableCell>
                      <Badge variant="secondary" className={config.className}>
                        <Icon className="h-3 w-3 mr-1" />
                        {config.label}
                      </Badge>
                    </TableCell>
                    <TableCell>
                      {resource.isVip && (
                        <Badge className="bg-warning text-warning-foreground">
                          <Crown className="h-3 w-3 mr-1" />
                          VIP
                        </Badge>
                      )}
                    </TableCell>
                    <TableCell className="text-muted-foreground text-sm">{resource.createdAt}</TableCell>
                    <TableCell className="text-right">
                      <div className="flex flex-wrap justify-end gap-2">
                        <Button
                          variant="ghost"
                          size="icon"
                          onClick={() => window.open(resource.link, "_blank")}
                        >
                          <ExternalLink className="h-4 w-4" />
                        </Button>
                        <Button
                          size="icon"
                          onClick={() => handleRowSave(resource)}
                          disabled={!entry?.dirty || entry?.saving}
                        >
                          <Save className="h-4 w-4" />
                        </Button>
                        <Button
                          variant="destructive"
                          size="icon"
                          onClick={() => removeResource(resource.id)}
                        >
                          <Trash2 className="h-4 w-4" />
                        </Button>
                      </div>
                    </TableCell>
                  </TableRow>
                )
              })}
              {loading && (
                <TableRow>
                  <TableCell colSpan={5} className="text-center py-8 text-muted-foreground">
                    正在加载数据...
                  </TableCell>
                </TableRow>
              )}
              {!loading && filteredResources.length === 0 && (
                <TableRow>
                  <TableCell colSpan={5} className="text-center py-8 text-muted-foreground">
                    未找到资源
                  </TableCell>
                </TableRow>
              )}
            </TableBody>
          </Table>
        </div>
        </CardContent>
      </Card>
      <Dialog open={Boolean(editing)} onOpenChange={(open) => !open && !savingEdit && setEditing(null)}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>编辑资源</DialogTitle>
          </DialogHeader>
          <div className="space-y-4">
            <div className="space-y-2">
              <Label htmlFor="edit-title">标题</Label>
              <Input
                id="edit-title"
                value={editTitle}
                onChange={(e) => setEditTitle(e.target.value)}
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="edit-link">Telegram 链接</Label>
              <Input
                id="edit-link"
                value={editLink}
                onChange={(e) => setEditLink(e.target.value)}
                placeholder="https://t.me/..."
              />
            </div>
          </div>
          <DialogFooter>
            <Button variant="outline" onClick={() => !savingEdit && setEditing(null)}>
              取消
            </Button>
            <Button onClick={handleEditSave} disabled={savingEdit}>
              {savingEdit ? "保存中..." : "保存"}
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </>
  )
}
