"use client"

import { useState } from "react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Button } from "@/components/ui/button"
import { Switch } from "@/components/ui/switch"
import { Badge } from "@/components/ui/badge"
import { UploadDropzone } from "./upload-dropzone"
import { Heart, ChevronRight, ChevronLeft, Check, Link2, Copy } from "lucide-react"
import { uploadComic } from "@/lib/api"
import { useToast } from "@/components/ui/use-toast"

type Step = 1 | 2 | 3

export function ComicUploadWizard({ className = "" }: { className?: string } = {}) {
  const [step, setStep] = useState<Step>(1)
  const [title, setTitle] = useState("")
  const [isVip, setIsVip] = useState(false)
  const [images, setImages] = useState<File[]>([])
  const [deepLink, setDeepLink] = useState("")
  const [previewLink, setPreviewLink] = useState("")
  const [previewInput, setPreviewInput] = useState("")
  const [isUploading, setIsUploading] = useState(false)
  const { toast } = useToast()

  const canProceedStep1 = title.trim().length > 0 && previewInput.trim().length > 0
  const canProceedStep2 = images.length > 0

  const handleFinish = async () => {
    if (!canProceedStep1 || !canProceedStep2 || isUploading) return
    try {
      setIsUploading(true)
      const result = await uploadComic({
        title: title.trim(),
        isVip,
        files: images,
        previewLink: previewInput.trim(),
      })
      setDeepLink(result.deepLink)
      setPreviewLink(result.previewLink)
      setStep(3)
      toast({
        title: "上传成功",
        description: `共 ${result.pages} 张图片，已记录预览链接`,
      })
    } catch (error) {
      const message = error instanceof Error ? error.message : "上传失败"
      toast({
        title: "上传失败",
        description: message,
        variant: "destructive",
      })
    } finally {
      setIsUploading(false)
    }
  }

  const copyLink = () => {
    navigator.clipboard.writeText(deepLink)
  }

  const resetWizard = () => {
    setStep(1)
    setTitle("")
    setIsVip(false)
    setImages([])
    setDeepLink("")
    setPreviewLink("")
    setPreviewInput("")
  }

  return (
    <Card className={className}>
      <CardHeader>
        <CardTitle className="flex items-center gap-2 text-card-foreground">
          <Heart className="h-5 w-5 text-chart-5" />
          上传漫画
        </CardTitle>
        <div className="flex items-center gap-2 mt-4">
          {[1, 2, 3].map((s) => (
            <div key={s} className="flex items-center">
              <div
                className={`w-8 h-8 rounded-full flex items-center justify-center text-sm font-medium transition-colors ${step >= s ? "bg-primary text-primary-foreground" : "bg-muted text-muted-foreground"}`}
              >
                {step > s ? <Check className="h-4 w-4" /> : s}
              </div>
              {s < 3 && <div className={`w-12 h-0.5 mx-1 ${step > s ? "bg-primary" : "bg-muted"}`}></div>}
            </div>
          ))}
        </div>
      </CardHeader>
      <CardContent>
        {step === 1 && (
          <div className="space-y-5">
            <div className="space-y-2">
              <Label htmlFor="comic-title">漫画标题</Label>
              <Input
                id="comic-title"
                placeholder="输入漫画标题..."
                value={title}
                onChange={(e) => setTitle(e.target.value)}
              />
            </div>

            <div className="space-y-2">
              <Label htmlFor="comic-preview-link">预览频道消息链接</Label>
              <Input
                id="comic-preview-link"
                placeholder="https://t.me/c/xxxxxxxxxx/123"
                value={previewInput}
                onChange={(e) => setPreviewInput(e.target.value)}
              />
              <p className="text-xs text-muted-foreground">
                先在预览频道发送含有深度链接的消息，然后复制该消息的链接粘贴到这里。
              </p>
            </div>

            <div className="flex items-center justify-between p-3 rounded-lg bg-muted">
              <div className="space-y-0.5">
                <Label htmlFor="comic-vip" className="cursor-pointer">
                  仅限 VIP
                </Label>
                <p className="text-xs text-muted-foreground">限制为 VIP 会员访问</p>
              </div>
              <Switch id="comic-vip" checked={isVip} onCheckedChange={setIsVip} />
            </div>

            <Button className="w-full" onClick={() => setStep(2)} disabled={!canProceedStep1}>
              下一步：上传图片
              <ChevronRight className="h-4 w-4 ml-2" />
            </Button>
          </div>
        )}

        {step === 2 && (
          <div className="space-y-5">
            <UploadDropzone images={images} onImagesChange={setImages} maxImages={200} />

            <div className="flex gap-3">
              <Button variant="outline" onClick={() => setStep(1)} className="flex-1">
                <ChevronLeft className="h-4 w-4 mr-2" />
                返回
              </Button>
              <Button onClick={handleFinish} disabled={!canProceedStep2 || isUploading} className="flex-1">
                {isUploading ? "上传中..." : "完成上传"}
                <ChevronRight className="h-4 w-4 ml-2" />
              </Button>
            </div>
          </div>
        )}

        {step === 3 && (
          <div className="space-y-5 text-center">
            <div className="w-16 h-16 rounded-full bg-success/10 flex items-center justify-center mx-auto">
              <Check className="h-8 w-8 text-success" />
            </div>

            <div>
              <h3 className="font-semibold text-lg text-card-foreground">{title}</h3>
              <p className="text-sm text-muted-foreground mt-1">
                已上传 {images.length} 张图片
                {isVip && " • 仅限 VIP"}
              </p>
            </div>

            <div className="p-4 bg-muted rounded-lg space-y-2">
              <Label className="text-xs text-muted-foreground">深度链接</Label>
              <div className="flex gap-2">
                <Input value={deepLink} readOnly className="font-mono text-xs" />
                <Button variant="outline" size="icon" onClick={copyLink}>
                  <Copy className="h-4 w-4" />
                </Button>
              </div>
            </div>

            <div className="p-4 bg-muted rounded-lg space-y-2">
              <Label className="text-xs text-muted-foreground">预览频道链接</Label>
              <Input value={previewLink} readOnly className="font-mono text-xs" />
            </div>

            <Badge className="bg-success text-success-foreground">
              <Link2 className="h-3 w-3 mr-1" />
              已准备好分享
            </Badge>

            <Button variant="outline" onClick={resetWizard} className="w-full bg-transparent">
              上传另一部漫画
            </Button>
          </div>
        )}
      </CardContent>
    </Card>
  )
}

function cn(...classes: (string | boolean | undefined)[]) {
  return classes.filter(Boolean).join(" ")
}
